.yalla-header {
  .navbar {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 99999;
    box-shadow: 0 1px 3px #000;
    background-color: #ff5a00;
    color: $white;
    .yalla-header__logo-box {
      .navbar-brand {
        img {
          width: 120px;
        }
      }
    }
    .main-navbar {
      .nav-item-close {
        display: none;
      }
      li a,
      .sign-nav li a {
        color: $white;
      }
    }
    .sign-nav {
      form {
        .search-container {
          margin-top: 5px;
          border-radius: 7px;
          overflow: hidden;
          position: relative;
          .search-input {
            width: 100%;
            height: 35px;
            padding: 2px 12px;
            border-radius: 25px;
            font-size: 14px;
            border: 1px solid #a6a6a6;
            &:focus-visible {
              outline: none;
            }
          }
          .search-button {
            position: absolute;
            background: transparent;
            border: none;
            left: 8px;
            top: 50%;
            cursor: pointer;
            -webkit-transform: translateY(-43%);
            transform: translateY(-43%);
            svg {
              path {
                fill: rgb(131, 131, 131);
              }
            }
          }
        }
      }
      .nav-item {
        .img-language {
          width: 30px;
          height: 30px;
        }
        a {
          color: #fff;
          svg {
            path {
              fill: $white;
            }
          }
        }
      }
    }
  }
}

@media (min-width: 992px) {
}

@media (max-width: 991.98px) {
  .yalla-header .navbar {
    padding: 0.1rem 1rem;
    .nav-box {
      justify-content: center;
    }

    // -- mobile menu
    .navbar-collapse {
      background-color: #fff;
      position: absolute;
      top: 0;
      right: -80%;
      color: $gray;
      width: 80%;
      height: 100vh;
      display: block;
      transition: all 0.3s ease-in-out;
      z-index: 9999999;
      overflow: scroll;

      .nav-item {
        padding: 1px 5px;
        &:not(:last-of-type) {
          border-bottom: 1px solid #d1cece;
        }

        a {
          color: $black !important;
        }
      }
      .nav-item-close {
        display: block;
        border-bottom: none !important;
        padding: 15px 15px;

        .btn-close {
          border-radius: 50%;
          width: 30px;
          height: 30px;
          border: 1px solid $black;
          background-color: transparent;
        }
      }
    }
    .navbar-collapse.show {
      right: 0%;
      transition: all 0.3s ease-in-out;
    }
    .over-lay {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.385);
      z-index: 9999998;
      visibility: hidden;
      opacity: 0;
      transition: visibility 0s, opacity 0.5s linear;
    }
    .overlay-block {
      visibility: visible;
      opacity: 1;
    }
  }
  .navbar-toggler {
    position: absolute;
    right: -4px;
    top: 7px;

    &:focus {
      outline: none;
    }
    &:focus-visible {
      outline: none;
    }
    .navbar-toggler-icon {
      background-color: $white;
      display: block;
      width: 30px;
      height: 2px;
      margin: 6px 0;
    }
  }
}

